
<template>
  <div>
    <div class="article-content">
      <div class="content">
        <!-- -->
        <el-upload
          class="avatar-uploader"
          :action="serverUrl"
          name="img"
          :headers="header"
          :show-file-list="false"
          :on-success="uploadSuccess"
          :on-error="uploadError"
          :before-upload="beforeUpload"
        ></el-upload>

        <quill-editor
          class="editor"
          v-model="content"
          ref="myQuillEditor"
          :options="editorOption"
          @blur="onEditorBlur($event)"
          @focus="onEditorFocus($event)"
          @change="onEditorChange($event)"
        ></quill-editor>
      </div>
      <el-button type="primary" round @click="saveContent()"></el-button>
    </div>
  </div>
</template>
<script>
import axios from "@/axiosConfig.js";

import Quill from 'quill'
import { quillEditor} from "vue-quill-editor";
import {ImageExtend } from 'quill-image-extend-module'
import ImageResize from 'quill-image-resize-module'
import { ImageDrop } from 'quill-image-drop-module'
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

Quill.register('modules/ImageExtend', ImageExtend)
Quill.register('modules/imageResize', ImageResize)
Quill.register('modules/imageDrop', ImageDrop)
// 
const toolbarOptions = [
  ["bold", "italic", "underline", "strike"], //    
  ["blockquote", "code-block"], //   
  [{ header: 1 }, { header: 2 }], // 12 
  [{ list: "ordered" }, { list: "bullet" }], // 
  [{ script: "sub" }, { script: "super" }], // /
  [{ indent: "-1" }, { indent: "+1" }], // 
  // [{'direction': 'rtl'}],                         // 
  [{ size: ["small", false, "large", "huge"] }], // 
  [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
  [{ color: [] }, { background: [] }], // 
  [{ font: [] }], // 
  [{ align: [] }], // 
  ["clean"], // 
  ["link", "image", "video"] // 
];
 

export default {
  props: {
    /**/
    value: {
      type: String
    },
    /**/
    maxSize: {
      type: Number,
      default: 4000 //kb
    }
  },

  components: {
    quillEditor
  },

  data() {
    return {
      title: "",
      content: "",
      serverUrl: "/simBlogServer/imgUpload", // 
      // serverUrl: "/api/imgUpload", // 
      header: { token: sessionStorage.token }, // token
      quillUpdateImg: false, // loadingfalse,
      editorOption: {
        placeholder: "",
        theme: "snow", // or 'bubble'
        // placeholder: "",
        modules: {
          imageDrop: true,
          // 
          imageResize: {
            // displayStyles: {
            //   backgroundColor: 'black',
            //   border: 'none',
            //   color: 'white'
            // },
            // modules: [ 'Resize', 'DisplaySize', 'Toolbar' ]
          },
          //
          ImageExtend: {
            loading: true,
            name: 'img',
            action: "/api/imgUpload",
            response: (res) => {
              return res.url
            }
          },
          //
          toolbar: {
            container: toolbarOptions,
            // container: "#toolbar",
            handlers: {
              image: function(value) {
                if (value) {
                  // input
                  document.querySelector(".avatar-uploader input").click();
                } else {
                  this.quill.format("image", false);
                }
              },
              link: function(value) {
                if (value) {
                  var href = prompt('url');
                  this.quill.format("link", href);
                } else {
                  this.quill.format("link", false);
                }
              },
            }
          }
        }
      },
    };
  },

  methods: {
    saveContent() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          var articleData = {
            userId: this.$store.state.userStatus.userId,
            title: value,
            content: this.content
          };
          axios("POST", "/saveArticle", articleData)
            .then(result => {
              if (result.data == "0") {
                this.$message({ type: "success", message: "" });
              } else if (result.data == "1") {
                this.$message({ type: "info", message: "" });
              }
            })
            .catch(error => {
              this.$emit("open", ":" + error);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    onEditorBlur() {
      //
    },
    onEditorFocus() {
      //
    },
    onEditorChange() {
      //
      this.$emit("input", this.content);
    },
    // 
    beforeUpload() {
      // loading
      this.quillUpdateImg = true;
    },

    uploadSuccess(res) {
      // res
      // 
      let quill = this.$refs.myQuillEditor.quill;
      // 
      if (res.code == 200) {
        // 
        let length = quill.getSelection().index;
        //   res.url
        quill.insertEmbed(length, "image", res.url);
        // 
        quill.setSelection(length + 1);
      } else {
        this.$message.error("");
      }
      // loading
      this.quillUpdateImg = false;
    },
    // 
    uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    }
  },
  watch:{
  }
};
</script> 

<style>
.article-content {
  background: url("../assets/05.jpg");
  background: rgba(255, 255, 255, 0.2);
  /* padding: 100px 20px 0 20px; */
  width: 1560px;
  height: 1100px;
}
.content {
  height: 1000px;
  /* background: yellow; */
}
.editor {
  line-height: normal !important;
  height: 900px;
  padding: 0 20px 0 20px;
  color: white;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: ":";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}
</style>

